
import { computed, defineComponent, ref } from "vue";

import * as Connection from "../../src/connection";
import {
  UserControl,
  eventTypes,
  EventType,
} from "../../src/UserDataConfig/userControl";

import BtnHref from "../a/btn-href.vue";
import Row from "../a/row-component.vue";
import RowCell from "../a/row-cell.vue";
import RowCellRight from "../a/row-cell-right.vue";
import Card from "../a/card-component.vue";
import CardBody from "../a/card-body.vue";
import ServerInOutPortsSelect from "../a/serverIn-out-ports-select.vue";

export default defineComponent({
  components: {
    ServerInOutPortsSelect,
    Card,
    CardBody,
    BtnHref,
    Row,
    RowCell,
    RowCellRight,
  },
  props: {
    item: {
      type: UserControl,
      required: true,
    },
  },
  emits: ['ucchange', "ucsend"],
  setup(props,{ emit }) {
    const localItem = ref(Object.assign({}, props.item));
    const getMidiOutputIdByName = computed(()=>{
      return Connection.loginStatus.getMidiOutputIdByName(localItem.value.outputPortnName);
    })
    localItem.value.outputPortId = getMidiOutputIdByName.value;

    const dropdownlists = computed(() => {
      return Connection.loginStatus.userDataConfig.dropdownlists;
    });


    const getDropdownDescriptions = computed(() => {
      const ary: string[] = [];
      const names = props.item.dropDownDecriptions.split("\n");
      for (
        let i = parseInt(localItem.value.minVal);
        i <= parseInt(localItem.value.maxVal);
        i++
      ) {
        if (names[i] !== undefined) {
          ary.push(names[i]);
        } else {
          ary.push(i.toString());
        }
      }
      return ary;
    });

    const outPorts = computed(() => {
      return Connection.loginStatus.outPorts;
    });

    const mid64Val = computed(() => {
      const maxval = parseInt(localItem.value.maxVal);
      const minval = parseInt(localItem.value.minVal);
      return minval + Math.floor((maxval - minval + 1) / 2);
    });

    const getVal = computed({
      get: () => {
        if (localItem.value.is64Mode) {
          return (parseInt(localItem.value.inputVal) - mid64Val.value).toString();
        }
        return localItem.value.inputVal;
      },
      set: (n: string) => {
        const val = parseInt(n);
        if (localItem.value.is64Mode) {
          localItem.value.inputVal = (val + mid64Val.value).toString();
        } else {
          localItem.value.inputVal = val.toString();
        }
      },
    });

    function doInputVal(n: number) {
      const newVal = parseInt(localItem.value.inputVal) + n;
      if (
        newVal < parseInt(localItem.value.minVal) ||
        newVal > parseInt(localItem.value.maxVal)
      ) {
        return;
      }
      localItem.value.inputVal = newVal.toString();
      emit('ucchange', {inputVal: localItem.value.inputVal})
    }

    function doSend() {
      emit("ucsend", localItem.value);
      return;
    }

  

    return {
      eventTypes,
      EventType,
      doSend,
      doInputVal,
      getVal,
      outPorts,
      getDropdownDescriptions,
      dropdownlists,
      mid64Val,
      localItem,
      getMidiOutputIdByName
    };
  },
});
