
import { defineComponent, ref } from "vue";

import * as Connection from "../../src/connection";
import { arrayMove } from "../../src/Utils";

import ControlComponent from "@/components/usercontrol/control-component.vue";

import Page from "../a/page-component.vue";
import BtnHref from "../a/btn-href.vue";
import { UserControl } from "@/src/UserDataConfig/userControl";

export default defineComponent({
  components: {
    ControlComponent,
    Page,
    BtnHref,
  },
  setup() {
    const editMode = ref(false);

    const userControls = ref(
      Connection.loginStatus.userDataConfig.activePreset.userControls
    );
    
    const addUserControl = () => {
      userControls.value.push(new UserControl());
    };

    const am = (oldIdex: number, newIndex: number) => {
      arrayMove(userControls.value, oldIdex, newIndex);
    };

    const sendAll = () => {
      return Connection.loginStatus.userDataConfig.activePreset.sendAllUserControls();
    };

    const doChange = (
      e: typeof userControls.value[0],
      item: typeof userControls.value[0]
    ) => {
        Object.assign(item, e);
        if ("inputVal" in e){
          item.doSend();
        }
        
    };

    const doSend = (
      e: typeof userControls.value[0],
      item: typeof userControls.value[0]
    ) => {
        item.doSend();      
    };

    return {
      sendAll,
      am,
      addUserControl,
      userControls,
      editMode,
      doChange,
      doSend,
    };
  },
});
